@font-face {
    font-family: 'RubikRegular';
    src: url("../font/Rubik/Rubik-Regular.ttf'");
}

@font-face {
    font-family: 'RubikBold';
    src: url("../font/Rubik/Rubik-Bold.ttf");
}

@font-face {
    font-family: 'RubikLight';
    src: url("../font/Rubik/Rubik-Light.ttf");
}

@font-face {
    font-family: 'RubikMedium';
    src: url("../font/Rubik/Rubik-Medium.ttf");
}

@font-face {
    font-family: 'RubikItalic';
    src: url("../font/Rubik/Rubik-Italic.ttf");
}

$body-link-color: #000; //  Originally: #1eb9e3, Latest: #07a5d9
//$body-font-family: RubikLight, sans-serif; // Originally: font:13px/25px Helvetica,Arial,sans-serif
$body-font-family: RubikLight, sans-serif; // Originally: font:13px/25px Helvetica,Arial,sans-serif
$body-font-size: 18px;
$body-font-color: #212121;
$body-line-height: 32px; // Originally: 25px

$post-link-color-hover: #3ecfff; // Originally: #036a8c

a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,canvas,caption,center,cite,code,dd,del,details,dfn,dialog,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,table,tbody,td,textarea,tfoot,th,thead,time,tr,tt,u,ul,var,video{
    margin:0;
    padding:0;
    font-size:100%;
    font-weight:inherit;
    font-style:inherit;
    text-align:inherit;
    background:0 0;
    border:0;
    font-family:inherit;
    outline:0;
    vertical-align:baseline
}

// Define the style of links in the body section
body a {
    color: $body-link-color;
    text-decoration: none;
}

body a:hover {
    text-decoration: underline;
}

h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
    color:#000;
}

h1 a:hover, h2 a:hover, h3 a:hover, h4 a:hover {
    color: #000; // PRE-2020: #1eb9e3
}

article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{
    display:block
}
ol,ul{
    list-style:none
}
blockquote,q{
    quotes:none
}
:focus{
    outline:0
}
table{
    border-collapse:collapse;
    border-spacing:0
}
img{
    height:auto;
    max-width:100%
}
.container{
    // Originally, max-width is set to 940px. But in order to maintain ~75 characters
    // per line, it might be better to downsize it
    max-width:800px;
    min-width:220px;
    margin:0 auto;
    -moz-box-sizing:content-box;
    -webkit-box-sizing:content-box;
    box-sizing:content-box
}
.col,.container .row{
    -moz-box-sizing:border-box;
    -webkit-box-sizing:border-box;
    box-sizing:border-box
}
.row{
    margin-bottom:1.5em
}
.col{
    display:block;
    float:right;
    width:100%
}
.col:first-child{
    margin-right:0
}
.clr {
  clear: both;
}
@media (min-width:768px){
    .col{
        margin-right:2%
    }
    .span_1{
        width:6.5%
    }
    .span_2{
        width:15%
    }
    .span_3{
        width:23.5%
    }
    .span_4{
        width:32%
    }
    .span_5{
        width:40.5%
    }
    .span_6{
        width:49%
    }
    .span_7{
        width:57.5%
    }
    .span_8{
        width:66%
    }
    .span_9{
        width:100% // PRE2020: 74.5%
    }
    .span_10{
        width:83%
    }
    .span_11{
        width:91.5%
    }
    .span_12{
        width:100%
    }
}
.count-1{
    clear:both;
    margin-right:0
}
body {
    color: $body-font-color;
    direction: rtl;
    unicode-bidi: embed;
    font-weight: 400;
    background: #fff;
    font-family: $body-font-family;
    font-size: $body-font-size;
    line-height: $body-line-height;
    overflow-x: hidden;
    height: 100%;
    nowhitespace: afterproperty
}

#box-wrap{
    margin:130px auto; /* PRE2020: 40px auto */
    min-height: calc(100vh - 100px);

}
