.dropdown-menu{
    margin:0;
    padding:0;
    list-style:none
}
.dropdown-menu .icon-angle-right{
    position:absolute;
    left:10px;
    top:50%;
    margin-top:-6px;
    color:#aaa;
    font-size:12px
}
.dropdown-menu>li{
    display:block;
    float:right
}
.dropdown-menu>li ul{
    min-width:200px
}
.dropdown-menu ul{
    position:absolute;
    right:0;
    top:100%;
    margin:10px 0 0 0;
    nowhitespace:afterproperty;
    opacity:0;
    transition:all .2s ease-in-out;
    visibility:hidden;
    z-index:99
}
.dropdown-menu ul ul{
    right:100%;
    top:0
}
.dropdown-menu ul ul li{
    width:100%
}
.dropdown-menu li{
    position:relative
}
.dropdown-menu li:hover>ul{
    margin:0;
    opacity:1;
    visibility:visible
}
.dropdown-menu a{
    display:block;
    position:relative;
    transition: color .2s ease-out;
}
#navigation .dropdown-menu .icon-angle-right{
    margin-top:-7px
}
#navigation .dropdown-menu>li{
    margin-right:20px
}
#navigation .current-menu-item>a:hover,#navigation .current-menu-parent>a,#navigation .current-menu-parent>a:hover,#navigation .dropdown-menu>.current-menu-item>a,#navigation .dropdown-menu>li.sfHover>a,#navigation .dropdown-menu>li>a:hover{
