.bubble{
    position:relative;
    padding:10px 15px;
    background:#eee;
    -webkit-border-radius:10px;
    -moz-border-radius:10px;
    border-radius:10px;
    border:#d6d6d6 solid 2px
}
.bubble:after{
    content:'';
    position:absolute;
    border-style:solid;
    border-width:14px 0 14px 21px;
    border-color:transparent #eee;
    display:block;
    width:0;
    z-index:1;
    right:-21px;
    top:12px
}
.bubble:before{
    content:'';
    position:absolute;
    border-style:solid;
    border-width:15px 0 15px 22px;
    border-color:transparent #d6d6d6;
    display:block;
    width:0;
    z-index:0;
    right:-24px;
    top:11px
}
